.jumbotron {
    height: 100%;
    display: flex;
    align-items: center;
}

.wrapper h6 {
    font-size: 24px;
    font-weight: 600;
}

.wrapper h1 {
    font-size: 58px;
    font-weight: 800;
}

.wrapper h3 {
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 25px;
}

.portrait {
    position: absolute;
    bottom: 0;
    right: -200px;
    height: 100%;
    z-index: -999;
}

@media(max-width: 1375px) {
    .portrait {
        right: -400px;
    }
}

.typingHeader {
    height: 69px;
    margin-bottom: 25px;
}