.wrapper {
    text-align: center;
}

.wrapper > h1 {
    font-weight: 800;
    font-size: 38px;
    margin-top: 80px;
    margin-bottom: 50px;
}

.wrapper > h3 {
    font-weight: 600;
    font-size: 26px;
    margin: 80px 0;
}

.wrapper h4 {
    font-weight: 600;
    font-size: 20px;
}

.wrapper > p {
    margin: 0 auto;
    margin-bottom: 50px;
    max-width: 1000px;
}

.statsCardDeck {
    max-width: 1000px;
    margin: 0 auto;
    margin-bottom: 80px;
}

.statsCardDeck > div {
    margin: 0 30px !important;
    background-color: rgba(0, 0, 0, 0.05);
}

.acknowledgmentsCardDeck {
    max-width: 1100px;
    margin: 0 auto;
    margin-bottom: 80px;
}

.acknowledgmentsCardDeck > div {
    margin: 0 50px !important;
    background-color: rgba(0, 0, 0, 0.05);
}

.acknowledgmentsCardDeck h6 {
    font-size: 24px;
    font-weight: 600;
}

.statsCardDeck h4 {
    font-size: 38px;
    font-weight: 700;
}

.map {
    width: 70%;
}

.goalsCol > h5 {
    margin: 50px 0;
    font-weight: 700;
}

.goalsCol > img {
    height: 300px;
}

.verticalAlign {
    display: flex;
    align-items: center;
}

.verticalAlign > div {
    margin: 0 auto;
}

.jobDescription {
    width: 500px;
    margin: 0 auto;
}