.wrapper {
    /* height: 100%; */
    text-align: center;
}

.wrapper > h1 {
    margin: 80px 0 60px 0;
    font-weight: 800;
}

.startText {
    margin-bottom: 30px;
    opacity: .35;
}

.timeline {
    position: absolute;
    transform: translate(-50%, 0);
    top: 300px;
}

.verticalLine {
    stroke: #454545;
    stroke-width: 7;
}

.topCircle {
    stroke: #454545;
    stroke-width: 5;
    fill: transparent;
}

.eventCircle {
    fill: #454545;
}

.right {
    text-align: left;
    margin-left: 55%;
}

.left {
    text-align: right;
}

.endText {
    opacity: .35;
}

.firstExperience {
    position: relative;
    margin-bottom: 250px;
    top: 135px;
}

@media(max-width: 768px) {
    .timeline {
        top: 343px;
    }
}